
$black:#101B24;
$ivory:#F8F4E7;
$blown:#525053;
$green:#274000;
$blue:#29516e;
$navy:#0d2030;
$skybule:#bdd6ce;
$skybule_clear:rgba(189,214,206,0.7);
$blown_clear:rgba(82,80,83,0.5);
$green_clear:rgba(39,64,0,0.5);
$navy_clear:rgba(13,32,48,0.5);

@mixin media_PC($width-media:1390px){
    @media only screen and
      (min-width:$width-media){
      @content;
    }
  }
  
@mixin media_tablet($width-media:768px){
  @media only screen and
    (min-width:$width-media){
    @content; 
    }
}
@mixin media_SP-wide($width-media:650px){
  @media only screen and
    (min-width:$width-media){
    @content; 
    }
}
@mixin hamburger{
    display: block;
    width: 24px;
    height: 2px;
    background-color:$black;
    @include media_PC{
      width: 36px;
    }
}
@mixin header_tablet{
  height: 80px;
}
@mixin layout_tablet{
  max-width: 750px;
  margin: 100px auto;
}
@mixin layout_PC {
  max-width: 920px;
  margin: 100px auto;
}
@mixin  layout_PCwide{
  max-width: 1300px;
  margin: 150px auto;
}
  

html {
    font-size: 62.5%;
    font-family: 'Shippori Mincho', serif;
    color:$black;
    font-weight: normal;
}
a{
  color : inherit;
  text-decoration : none;
}
ul{
  list-style: none;
}